import React, { Component } from 'react';
import VexFlow from './components/VexFlow';
import Vex from 'vexflow';
const VF = Vex.Flow;
import './App.css';

export default class App extends Component {

    constructor(props) {
        super(props);

        this.state = {
            notes: this.notes.slice(0)
        };
    }

    shuffle() {

        const notes = this.notes.slice(0);
        notes.sort((a, b) => Math.random() < Math.random());
        this.setState({ notes })
    }

    render() {

        return (
            <div className='App'>

                <button onClick={this.shuffle.bind(this)}>

                    Shuffle.
                </button>

                <VexFlow
                    height={800}
                    width={800}
                    font={[ 'Arial', 10, '' ]}
                    stave={this.stave}
                    voice={this.voice}
                />
            </div>
        );
    }

    get stave() {

        const stave = new VF.Stave(10, 40, 400);
        stave.addClef('treble').addTimeSignature('4/4');
        return stave;
    }

    get notes() {

        return [
            new VF.StaveNote({ keys: ["c/4", "e/4", "g/4"], duration: "q" }),
            new VF.StaveNote({ keys: ["c/4"], duration: "q" }),
            new VF.StaveNote({ keys: ["d/4"], duration: "q" }),
            new VF.StaveNote({ keys: ["b/4"], duration: "qr" }),
        ]
    }

    get voice() {

        var voice = new VF.Voice({num_beats: 4,  beat_value: 4});
        voice.addTickables(this.state.notes);
        var formatter = new VF.Formatter().joinVoices([voice]).format([voice], 400);
        return voice;
    }
}



